import React from "react";
import ClassNames from "classnames";
import CartItems from "./CartItem.js";
import {countItems} from "./utils.js";
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';


class Cart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cart: this.props.cart
    };
  }
  calculateTotal() {
    return this.props.cart
      .map(element => {
        return parseInt(element.price) * parseInt(element.numbers);
      })
      .reduce((results, currentValue) => {
        return results + currentValue;
      }, 0);
  }  
  

  render() {
    let cart = ClassNames({
      CartList: true,
      menuinactive: !this.props.cartOn,
      menuactive: this.props.cartOn
    });
      let Total = this.props.cart ? this.calculateTotal() : "0";
      console.log("il cart è:"+ this.props.cart);
      let totalItems= countItems(this.props.cart);
      console.log("il totale calcolato è:"+ totalItems);
      let carrello = this.props.cart.map(element => {
        return <CartItems data={element} updateCart={(data,toDo) => this.props.updateCart(data,toDo)}/> 
        
      });
      
    return (
      <div className={cart}>
        <div className="cartProducts">
        <ReactCSSTransitionGroup
          transitionName="fade"
          transitionEnterTimeout={300}
          transitionLeaveTimeout={300}>
        {carrello}
        </ReactCSSTransitionGroup> 
        </div>
        <div className="flex items-center justify-between cart my-2 border border-gray-800 font-bold">
          <div><button className="bg-gray-900 rounded text-white px-2" onClick={()=> this.props.erase()}>Clear</button> </div>
          <div><span className="font-normal"> Oggetti:</span> {totalItems}</div>
          <div><span className="font-normal"> Totale:</span> {Total} €</div>
        </div>
      </div>
    );
  }
}

export default Cart;
