import React from "react";
import Header from "./Header.js";
import MainProduct from "./MainProduct.js";
import Product from "./Product.js";
import ClassNames from "classnames";
import Cart from "./Cart.js";
import { getProducts, countItems } from "./utils.js";

class ShoppingCart extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			menuOn: false,
			cartOn: false,
			products: null,
			cart: []
		};
	}




	// funzione che accetta un Array, un obj e un "comando" per aggiungere, togliere o aggiornare
	// l'array del carrello
	arrangeCart(arrayState, newObj, toDo) {
		newObj.numbers = newObj.numbers === "" ? 1 : newObj.numbers;
		for (let i in arrayState) {
			if (arrayState[i].product === newObj.product) {
				switch (toDo) {
					case "push":
						arrayState[i].numbers =
							parseInt(newObj.numbers) + parseInt(arrayState[i].numbers);
						break;
					case "pull":
						arrayState.splice(i, 1);
						break;
					case "update":
						arrayState[i].numbers = parseInt(newObj.numbers);
						break;
				}
				return arrayState;
			}
		}
		return arrayState.push(newObj);
	}


	updateCart(data, toDo) {
		let updateCart = this.state.cart.slice();
		this.arrangeCart(updateCart, data, toDo);
		this.setState({
			cart: updateCart
		});
	}

	openCart() {
		this.setState({
			cartOn: !this.state.cartOn,
			menuOn: false
		});
	}

	openMenu() {
		this.setState({
			menuOn: !this.state.menuOn,
			cartOn: false
		});
	}

	helloProducts(json) {
		this.setState({
			products: json
		});
	}
	componentDidMount() {
		getProducts(json => this.helloProducts(json), "https://thetrickster87.github.io/Portfolio/E-cart/lista-prodotti.json");
	}
	render() {
		let menu = ClassNames({
			menulang: true,
			"menuinactive ": !this.state.menuOn,
			menuactive: this.state.menuOn
		});
		let prodotti = [];
		if (this.state.products) {
			prodotti = this.state.products.map(element => {
				return (
					<Product
						key={element._id}
						data={element}
						updateCart={(data, toDo) => this.updateCart(data, "push")}
					/>
				);
			});
		}

		// SOMMO TUTTE LE QUANTITA DEGLI ITEM NEL MIO CARRELLO
		let cartItems = countItems(this.state.cart);
		return (
			<div>
				<Header>
					<div>
						<img className="h-8" src="./apple.png" alt="applelogo" />
					</div>
					<div>
						<ul className="flex items-center lista">
							<li>Home</li>
							<li>Prodotti</li>
							<li>Novità</li>
							<li>Black Friday</li>
						</ul>
					</div>
					<nav className="flex text-gray-800 items-center justify-end ">
						<button
							className=" rounded focus:outline-none relative px-2 py-1 mr-1 flex items-center border-white border-2 hover:bg-gray-600"
							onClick={() => this.openCart()}
						>
							<div className="relative">
								<div className="CartItem">{cartItems}</div>
								<img className="w-6 relative" src="./cart.png" alt="s-cart" />
							</div>
						</button>
						<Cart
							cartOn={this.state.cartOn}
							cart={this.state.cart}
							updateCart={(data, toDo) => this.updateCart(data, toDo)}
							erase={() => {
								this.setState({
									cart: []
								});
							}}
						/>

						<div>
							<button
								className="rounded focus:outline-none px-2 py-1 mr-1 flex items-center border-white border-2 hover:bg-gray-600"
								onClick={() => this.openMenu()}
							>
								<div className="relative">
									{" "}
									<img className="w-6" src="./cog-white.png" alt="s-cart" />
								</div>
							</button>
							<div className={menu}>
								<ul>
									<li>Italiano</li>
									<li>Inglese</li>
									<li>Giapponese</li>
									<li>Tedesco</li>
								</ul>
							</div>
						</div>
					</nav>
				</Header>

				<div className="pt-16">
					{/* </div> <div className="flex flex-col items-center h-full"> */}

					<MainProduct />
					<div className="productgrid h-full">{prodotti}</div>
				</div>
				<footer className="bg-gray-800 relative text-white bottom-0 w-full text-sm text-gray-500">
					Copyright © 2019 Apple Inc.
				</footer>
			</div>
		);
	}


}

export default ShoppingCart;
