import React from "react";
import ClassNames from "classnames";

class CartItem extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
      qlable: this.props.data.numbers,
      displayOn:true
		};
	}

  deleteThis(){
  this.setState({
    displayOn:false
  })
  return this.props.updateCart(this.props.data, "pull");  

  }



	handleInput(event) {
	
	let update= this.props.data;
	update.numbers=parseInt(event.target.value);
		//this.setState({ controlledlabel: event.target.value });
		return this.props.updateCart(update, "update")
	}


  componentDidUpdate(previousProps) {
     console.log("numero della props precedente:" + previousProps.data.numbers); 
   /*  console.log("numero di questa props:" + this.props.data.numbers); */
   console.log("sono passato da Update");
		if (this.state.qlable !== this.props.data.numbers ) {
        this.setState({
          qlable:this.props.data.numbers
        });
    }
  }
  

	render() {
    //non funziona l animazione di uscita della list del carrello, probabilmente viene rimosso
    // il componente prima di eseguire  l' animazione
    let line= ClassNames({
      "flex items-center justify-between cart my-2 border border-gray-800": true,
      "exitCartLine": this.displayOn
    });

		return (
			<div
				key={this.props.data.product}
				className={line}
			>
				{" "}
				<div className="flex justify-center items-center">
					<div className=" flex items-center justify-center">
						<div className="w-10 md:w-16 lg:w-20 flex items-center mr-2">
							<img
								src={this.props.data.picture}
								alt={this.props.data.product}
							/>
						</div>
					</div>
					<div className="font-bold">{this.props.data.product}</div>
				</div>
				<div className=" flex  items-center justify-between">
					<div className="text-base mr-2 bg-gray-900 text-white rounded px-2">
						{this.props.data.price} €{" "}
					</div>
					<div className="font-bold">
						<label className="block w-20">
							<input
								className=" text-center bg-white focus:outline-none focus:shadow-outline border border-gray-300 rounded-lg py-1 px-2 block w-full appearance-none leading-normal"
								type="number"
								min="1"
								value={this.state.qlable}
								onChange={event => this.handleInput(event)}
								placeholder="Quantità"
							/>
						</label>

						{/* {this.props.data.numbers} */}
					</div>
				</div>
				<div>
					<div className="flex">
						{/* <div>
							<button className="rounded-full focus:outline-none p-2 hover:bg-blue-700">
								<img className="w-6" src="./update.png" alt="bin" />
							</button>
						</div> */}
						<div>
              <button 
              onClick={(data,toDo)=> this.deleteThis()}
              className="rounded-full focus:outline-none p-2 hover:bg-blue-700">
								<img className="w-6" src="./bin.png" alt="bin" />
							</button>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default CartItem;
