import React from "react";

class Product extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      inputlable: ""
    };
  }

  handleInput(event) {
    this.setState({ inputlable: event.target.value });
  }

  render() {
    let shorttext = this.props.data.about.slice(0, 140);
    let item= this.props.data;
    item.numbers= this.state.inputlable=== "" ? 1: this.state.inputlable ;
    return (
      <div className="flex flex-col items-center w-full p-6">
        <div className="flex flex-col md:flex-row  mt-10 bg-gray-300 p-6 rounded-lg shadow-lg">
          <div className="md:w-1/2 flex items-center justify-center ">
            <img className="shadow-lg" src={this.props.data.picture} alt={this.props.data.product} />
          </div>
          <div className="md:w-1/2 mt-4 p-2 px-4 flex flex-col items-left">
            <div className="flex items-center"></div>
            <h1 className="text-2xl font-bold mt-4">
              {this.props.data.product}
              <span className="text-base font-normal">
                <br/><span className="italic">by {this.props.data.owner}</span>
              </span>
            </h1>
            <h1 className="text-sm lg:text-base mt-4">{shorttext}</h1>
            <h1 className="text-sm lg:text-xl mt-4">
              <span className="font-bold">Prezzo:</span>
              <span>{this.props.data.price}€</span>
            </h1>
            <div className="flex items-center mt-4 ">
              <div className="w-1/2 ">
                <label>
                  <input
                    className="bg-white focus:outline-none focus:shadow-outline border border-gray-300 rounded-lg py-2 px-4 block w-full appearance-none leading-normal"
                    type="number"
                    min="1"
                    value={this.state.inputlable}
                    onChange={event => this.handleInput(event)}
                    id="female"
                    placeholder="Quantità"
                  />
                </label>
              </div>
              <div className="w-1/2 flex justify-center">
                <button
                  onClick={(data) =>{
                    this.props.updateCart(                   
                       {
                      product: this.props.data.product,
                      price: this.props.data.price,
                      picture: this.props.data.picture,
                      numbers: this.state.inputlable,
                      about:this.props.data.about,
                      owner:this.props.data.owner
                    } 
                   
                    );
                  }}
                  className="bg-gray-800 text-gray-800 p-2 rounded  hover:bg-gray-500 border-2 border-gray-400 hover:border-gray-900"
                >
                 <img className="w-6" src="./cart.png" alt="cart"/>
                </button>
              </div>
            </div>
          </div>
        </div>
        <div className="bg-gray-200 p-4 lg:w-2/3 mt-8">
          {this.props.data.about}
        </div>
      </div>
    );
  }
}

export default Product;
